
import { defineComponent, reactive, ref } from "vue";
import VideoProjectionStream from "@/components/CameraView/VideoProjectionStream.vue";
import VideoStream from "@/components/CameraView/VideoStream.vue";
export default defineComponent({
  name: "VisionTest",
  components: {
    VideoProjectionStream,
    VideoStream
  },
  setup() {
    const wsProtocol = ref("");
    wsProtocol.value =
      window.location.protocol === "https:" ? "wss://" : "ws://";
    const videoList = reactive(
      [] as {
        ipAddress: string;
        extendAddress: string;
        projected: boolean;
        key: number;
      }[]
    );
    let baseAddress = "";
    if (
      process.env.NODE_ENV == "development" &&
      process.env.VUE_APP_DEVICE_ADDRESS
    ) {
      baseAddress = process.env.VUE_APP_DEVICE_ADDRESS;
    } else {
      baseAddress = window.location.host;
    }
    const addVideoBlock = () => {
      videoList.push({
        ipAddress: baseAddress,
        extendAddress: "",
        projected: false,
        key: new Date().getTime()
      });
    };
    const removeVideoBlock = (key: number) => {
      const index = videoList.findIndex(video => video.key === key);
      if (index > -1) {
        videoList.splice(index, 1);
      }
    };
    return {
      videoList,
      baseAddress,
      addVideoBlock,
      removeVideoBlock,
      wsProtocol
    };
  }
});
