<template>
  <div class="u-content">
    <h2><i class="fa fa-eye"></i> | Vision</h2>
  </div>
  <div class="videoSelectors">
    <div class="selector" v-for="video in videoList" :key="video.key">
      <i
        v-if="!video.projected"
        class="fa fa-expand"
        @click="video.projected = true"
      ></i>
      <i v-else class="fa fa-globe" @click="video.projected = false"></i>
      <input class="ip" type="text" v-model.lazy="video.ipAddress" />
      <input class="ext" type="text" v-model.lazy="video.extendAddress" />
    </div>

    <div class="adder" @click="addVideoBlock">
      <i class="fa fa-plus"></i>
    </div>
  </div>
  <div class="videoBlocks">
    <div class="videoBlock" v-for="video in videoList" :key="video.key">
      <div class="videoBar">
        <div>
          <i
            v-if="!video.projected"
            class="fa fa-expand"
            @click="video.projected = true"
          ></i>
          <i v-else class="fa fa-globe" @click="video.projected = false"></i>
        </div>
        <div style="color:darkgreen;">{{ video.address }}</div>
        <div style="color:red;" @click="removeVideoBlock(video.key)">
          <i class="fa fa-times"></i>
        </div>
      </div>
      <video-projection-stream
        v-if="video.projected"
        :wsAddress="wsProtocol + video.ipAddress + video.extendAddress"
        :size="400"
        :offset="32"
        :fov="2"
      ></video-projection-stream>
      <video-stream
        v-else
        :wsAddress="wsProtocol + video.ipAddress + video.extendAddress"
      ></video-stream>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from "vue";
import VideoProjectionStream from "@/components/CameraView/VideoProjectionStream.vue";
import VideoStream from "@/components/CameraView/VideoStream.vue";
export default defineComponent({
  name: "VisionTest",
  components: {
    VideoProjectionStream,
    VideoStream
  },
  setup() {
    const wsProtocol = ref("");
    wsProtocol.value =
      window.location.protocol === "https:" ? "wss://" : "ws://";
    const videoList = reactive(
      [] as {
        ipAddress: string;
        extendAddress: string;
        projected: boolean;
        key: number;
      }[]
    );
    let baseAddress = "";
    if (
      process.env.NODE_ENV == "development" &&
      process.env.VUE_APP_DEVICE_ADDRESS
    ) {
      baseAddress = process.env.VUE_APP_DEVICE_ADDRESS;
    } else {
      baseAddress = window.location.host;
    }
    const addVideoBlock = () => {
      videoList.push({
        ipAddress: baseAddress,
        extendAddress: "",
        projected: false,
        key: new Date().getTime()
      });
    };
    const removeVideoBlock = (key: number) => {
      const index = videoList.findIndex(video => video.key === key);
      if (index > -1) {
        videoList.splice(index, 1);
      }
    };
    return {
      videoList,
      baseAddress,
      addVideoBlock,
      removeVideoBlock,
      wsProtocol
    };
  }
});
</script>

